import Vue from 'vue'
import Router from 'vue-router'
import record from './views/record.vue'
import report from './views/report.vue'
import calculate from './views/calculate.vue'
import summary_month from './views/summary_month.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/record',
      name: 'บันทึก',
      component: record
    },
    {
      path: '/report',
      name: 'รายบันทึก',
      component: report
    },
    {
      path: '/calculate',
      name: 'ประเมินค่าใช้จ่าย',
      component: calculate
    },
    {
      path: '/summary_month',
      name: 'ค่าใช้จ่ายรายเดือน',
      component: summary_month
    }
       
  ]
})
