<template>
<div>
<div>
  <b-row>
      <b-col cols="4" ></b-col>
      <b-col cols="2" class="set_c f_s_80">
          <b-form-input type="date" v-model="value_month"></b-form-input>
      </b-col>
      <b-col cols="2" class="set_l f_s_80">
          <b-button size="sm" variant="primary"  v-on:click="new_load_db">ค้นหา</b-button>
      </b-col>
      <b-col cols="4" ></b-col>
  </b-row>
</div>
<br>
<div>
  <b-row>
      <b-col cols="2" ></b-col>
      <b-col cols="8" class="set_c f_s_80" v-if="item_record2!=''">
          <b-table 
          :items="item_record2"  
          :fields="field_record2"  
          striped bordered small 
          sticky-header="450px"
          head-variant="dark"
          >
          <div slot="index" slot-scope="data" > {{ data.index+1 }} </div >
          </b-table>
      </b-col>
      <b-col cols="2" ></b-col>
  </b-row>
</div>

</div>
</template>

<script>
export default {
data() {
      return {
        field_record2: [
          { key: 'index', label: '#' },
          { key: 'category', label: 'Category' , class:'text-left'},
          { key: 'sub_category', label: 'Sub Category' , class:'text-left'},
          { key: 'action_m', label: 'Action'},      
          { key: 'date_cal', label: 'Date Calulate'},
          { key: 'total', label: 'จำนวน', class:'text-right'},
        ],
        item_record2: [],
        value_month: '',
      }
},
methods: {
    load_db_record2() {
        var self= this;
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/summary_month/db_record2.php?date="+self.value_month;
        axios.get(link)
        .then(function (response) {
            self.item_record2=response.data;
            //console.log(response.data);
        })
        .catch(function (error) {
            console.log(error);
        })
    },
    new_load_db(){
        var self= this;
        self.load_db_record2();
    },
    get_time(){
        var self= this;
        var mm;
        var date = new Date();
        if(date.getMonth()<10){
            mm="0"+(date.getMonth()+1);
        }else{
            mm=(date.getMonth()+1);
        }
        self.value_month=date.getFullYear()+"-"+mm+"-01";
        self.load_db_record2();
        //console.log(self.value_month);
    }
},
mounted() {
    var self= this;
    self.get_time();
    //self.load_db_record();
    //self.load_db_record2();
}

}
</script>