<template>
<div>

<div>
  <b-row>
      <b-col cols="4" ></b-col>
      <b-col cols="4" class="set_c f_s_80" v-if="item_record!=''">
          <b-table 
          :items="item_record"  
          :fields="field_record"  
          striped bordered small
          head-variant="dark"
          >
          <div slot="index" slot-scope="data" > {{ data.index+1 }} </div >
          </b-table>
      </b-col>
      <b-col cols="4" ></b-col>
  </b-row>
</div>

</div>
</template>

<script>
export default {
data() {
      return {
        field_record: [
          { key: 'index', label: '#' },
          { key: 'date_cal', label: 'เดือนที่ต้องจ่าย'},
          { key: 'total', label: 'จำนวน', class:'text-right'}
        ],
        item_record: [],
      }
},
methods: {
    load_db_record() {
        var self= this;
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/calculate/db_record.php";
        axios.get(link)
        .then(function (response) {
            self.item_record=response.data;
            //console.log(response.data);
        })
        .catch(function (error) {
            console.log(error);
        })
    },
},
mounted() {
    var self= this;
    self.load_db_record();
   // self.load_db_record2();
}

}
</script>