<template>
<div>
<div>
<b-row>
    <b-col cols="4" class="set_r">หัวข้อ :</b-col>
    <b-col cols="4" class="set_l" v-if="option_ceta!=''">
        <b-form-select v-model="select_ceta" :options="option_ceta" size="sm"></b-form-select>
    </b-col>
    <b-col cols="4" class="set_l">
        <b-button pill size="sm" variant="info" v-b-modal.modal1>+</b-button>&nbsp;
        <b-button pill size="sm" v-on:click="load_db_cetagory">Re</b-button>
    </b-col>
</b-row>
 <b-row>
    <b-col cols="4" class="set_r">รายละเอียด :</b-col>
    <b-col cols="4" class="set_l" v-if="option_sub_ceta!=''">
        <b-form-select v-model="select_sub_ceta" :options="option_sub_ceta" size="sm"></b-form-select>
    </b-col>
    <b-col cols="4" class="set_l">
        <b-button pill size="sm" variant="primary" v-b-modal.modal2>+</b-button>&nbsp;
        <b-button pill size="sm" v-on:click="load_db_sub_cetagory">Re</b-button>
    </b-col>
</b-row>
<b-row>
    <b-col cols="4" class="set_r">ค่า :</b-col>
    <b-col cols="4" class="set_l">
        <b-form-select v-model="action_m" size="sm">
            <option value="+">+</option>
            <option value="-">-</option>
        </b-form-select>
    </b-col>
    <b-col cols="4" class="set_l"></b-col>
</b-row>
<b-row>
    <b-col cols="4" class="set_r">ราคา :</b-col>
    <b-col cols="4" class="set_l">
        <b-form-input type="number" size="sm" v-model="use_money"></b-form-input>
    </b-col>
    <b-col cols="4" class="set_l"></b-col>
</b-row>
<b-row>
<b-col cols="4" class="set_r">วันที่นับยอด :</b-col>
    <b-col cols="4" class="set_l">
        <b-form-input type="date" size="sm" v-model="date_cal"></b-form-input>
    </b-col>
    <b-col cols="4" class="set_l"></b-col>
</b-row>
<b-row>
<b-col cols="4" class="set_r">วันที่ใช้จ่าย :</b-col>
    <b-col cols="4" class="set_l">
        <b-form-input type="date" size="sm" v-model="date_save"></b-form-input>
    </b-col>
    <b-col cols="4" class="set_l"></b-col>
</b-row>
</div>
<div class="p_t_1p">
    <b-button size="sm" variant="success" v-on:click="inset_db_money">บันทึก</b-button>
</div>
<div class="p_t_1p">
<b-row>
    <b-col cols="2" ></b-col>
    <b-col cols="8" class="set_c f_s_80" v-if="item_record!=''">
        <b-table 
        :items="item_record"  
        :fields="field_record"  
        striped bordered small
        head-variant="dark"
        >
        <div  slot="index" slot-scope="data" > {{ data.index+1 }} </div >
        </b-table>
    </b-col>
    <b-col cols="2" ></b-col>
</b-row>
</div>

<!-- modal1 -->
<div>
  <b-modal id="modal1" title="เพิ่มหัวข้อ" hide-footer>
      <div class="set_c">
            <b-form-input v-model="val_cetagory" placeholder="ชื่อหัวข้อ"></b-form-input>
            <b-button size="sm" variant="success"  v-on:click="inset_db_cetagory">บันทึก</b-button>
        </div>
  </b-modal>
</div>
<!-- modal1 -->
<!-- modal2 -->
<div>
  <b-modal id="modal2" title="เพิ่มหัวข้อย่อย" hide-footer>
      <div class="set_c">
            <b-form-input v-model="val_sub_cetagory" placeholder="ชื่อหัวข้อย่อย"></b-form-input>
            <b-button size="sm" variant="success"  v-on:click="inset_db_sub_cetagory">บันทึก</b-button>
        </div>
  </b-modal>
</div>
<!-- modal2 -->

</div>
</template>

<script>
export default {
data() {
    return {
        select_ceta: '',
        option_ceta: [],
        select_sub_ceta: '',
        option_sub_ceta: [],
        field_record: [
          { key: 'index', label: '#' },
          { key: 'user_id', label: 'ID' },
          { key: 'category', label: 'Category', class:'text-left'},
          { key: 'sub_category', label: 'Sub Category', class:'text-left'},
          { key: 'action_m', label: 'Action'},      
          { key: 'use_money', label: 'Money', class:'text-right'},
          { key: 'date_cal', label: 'Date Calulate'},
          { key: 'date_save', label: 'Date Save'}
        ],
        item_record: [],
        val_cetagory:"",
        val_sub_cetagory:"",
        action_m:"-",
        use_money:0,
        date_cal:"",
        date_save:"",
    }
},
methods: {
    load_db_record() {
        var self= this;
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/record/db_record.php";
        axios.get(link)
        .then(function (response) {
            self.item_record=response.data;
            //console.log(response.data);
        })
        .catch(function (error) {
            console.log(error);
        })
    },
    load_db_cetagory() {
        var self= this;
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/record/db_cetagory.php";
        axios.get(link)
        .then(function (response) {
            self.option_ceta=response.data;
            //console.log(response.data);
        })
        .catch(function (error) {
            console.log(error);
        })
    },
    inset_db_cetagory() {
        var self= this;
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/record/insert_cetagory.php?name="+self.val_cetagory;
        axios.get(link)
        .then(function (response) {
            //console.log(response.data);
            self.load_db_cetagory();        
        })
        .catch(function (error) {
            console.log(error);
        })
    },
    load_db_sub_cetagory() {
        var self= this;
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/record/db_sub_cetagory.php";
        axios.get(link)
        .then(function (response) {
            self.option_sub_ceta=response.data;
            //console.log(response.data);
        })
        .catch(function (error) {
            console.log(error);
        })
    },
    inset_db_sub_cetagory(){
        var self= this;
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/record/insert_sub_cetagory.php?name="+self.val_sub_cetagory;
        axios.get(link)
        .then(function (response) {
            self.load_db_sub_cetagory(); 
            //console.log(response.data);         
        })
        .catch(function (error) {
            console.log(error);
        })
    },
    inset_db_money(){
        var self= this;
        var arr = {
        'user_id': 'M000001',
        'category': self.select_ceta,
        'sub_category': self.select_sub_ceta,
        'action_m': self.action_m,
        'use_money': self.use_money,
        'date_cal': self.date_cal,
        'date_save': self.date_save,
        };
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/record/inset_db_money.php?arr="+JSON.stringify(arr);
        axios.get(link)
        .then(function (response) {         
            console.log(response.data);    
            self.load_db_record();      
        })
        .catch(function (error) {
            console.log(error);
        })
    }

},
mounted() {
    var self= this;
    self.load_db_record();
    self.load_db_cetagory();
    self.load_db_sub_cetagory();
    //alert("....");
},

}
</script>
