<template>
<div>

<div>
  <b-row>
      <b-col cols="1" ></b-col>
      <b-col cols="10" class="set_c f_s_80" v-if="field_record!=''">
          <b-table 
          :items="item_record"  
          :fields="field_record"  
          striped bordered small
          sticky-header="500px"
          head-variant="dark"
          >
            <div  slot="index" slot-scope="data" > {{ data.index+1 }} </div >
        </b-table>
      </b-col>
      <b-col cols="1" ></b-col>
  </b-row>
</div>
<div>

</div>

</div>
</template>

<script>
export default {
data() {
      return {
        field_record: [
          { key: 'index', label: '#' },
          { key: 'user_id', label: 'ID' },
          { key: 'category', label: 'Category', class:'text-left'},
          { key: 'sub_category', label: 'Sub Category', class:'text-left'},
          { key: 'action_m', label: 'Action'},      
          { key: 'use_money', label: 'Money', class:'text-right'},
          { key: 'date_cal', label: 'Date Calulate'},
          { key: 'date_save', label: 'Date Save'}
        ],
        item_record: [],
      }
},
methods: {
    load_db_record() {
        var self= this;
        var xhttp = new XMLHttpRequest();
        xhttp.onreadystatechange = function() {
            if (this.readyState == 4 && this.status == 200) {
                self.item_record=JSON.parse(this.responseText);
            }else{}
        };
        var link ="https://saverecordmoney.000webhostapp.com/save_money/api/report/db_record.php";
        xhttp.open("POST", link , true);
        xhttp.send(); 
    },
},
mounted() {
    var self= this;
    self.load_db_record();
}

}
</script>